.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  grid-gap: 15px;
}
.card {
  border: none !important;
}

.flex {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
}

.flex,
.flex-wrap span {
  text-transform: uppercase;
}

.flex-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
